		width: 300px;
	}
	.thePrizeType,.theNum{
		.el-form-item {
			width: 240px !important;
			.el-form-item__content{
				// width: 100px !important;
				.el-input{
					width: 60%;
				}
				.el-select {
					width: 100%;
				}
			}
		}
	}
}

</style>
<style  lang="scss">
	.theForm{
		display: flex
	}
	.activityConfig .el-dialog__wrapper .el-dialog{
		width:1000px !important;
	}
	.thePrize,.theNum{
		margin-left: 20px;
	}
	.thePrize{
		.el-form-item{
			.el-input{
				width: 300px !important;
			}
			.el-select {
				width: 300px !important;
			}
		}
	}
	.time_hint{
		color: red;
	    font-size: 12px;
	    line-height: 1;
	    top: 70px;
		position: absolute;
	}
</style>



